import getTime from "../../../getTime.js";
export default {
	data() {
		return {
			curList: [],
			show: false,
			now: '',
			startTime: '',
			endTime: ''
		}
	},
	methods: {
		changetime(e) {
			if (e.target.dataset.time == 'start') {
				this.startTime = e.target.value;
			} else {
				this.endTime = e.target.value;
			}
		},
		showModel(title) {
			uni.showModal({
			    title: '提示',
			    content: title,
					showCancel: false,
			    success: function (res) {
			        if (res.confirm) {
			            
			        }
			    }
			});
		},
		
		showCurDetail(item) {
			
			console.log(item)
			uni.navigateTo({
				url: 'safeSearchDetail/safeSearchDetail?YHXH='+ item.YHXH + '&No=' + item.todoid + '&state=1&rymc='+item.RYMC
			})
		},
		getData() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Home/Get_SafeReport',
				data: {
					DWCD: self.$store.state.userInfo.DWCD,
					start_time: self.startTime,
					end_time: self.endTime,
				},
				method: 'GET',
				header: {
					'content-type': 'application/json' // 默认值
				},
				success(res) {
					
					if (res.data == '暂无数据') {
						self.curList = [];
					} else {
						var data = JSON.parse(res.data);
						console.log(data)
						self.curList = data;
						
						
					}
				}
			})
		},
		SearchMore() {
			uni.showLoading({
				title:"加载中",
				mask: true
			})
			this.getData()
			this.$nextTick(() => {
				uni.hideLoading()
			})
		},
	},
	mounted() {
		this.now = getTime.now();
		uni.showLoading({
			title:"加载中",
			mask: true
		})
		this.getData()
		this.$nextTick(() => {
			
			setTimeout(() => {
				uni.hideLoading();
				this.show = true;
			},1000)
		})
	}
}
